// ,   
import React, { useMemo, useState } from "react";
import $ from 'jquery';
import style from "./theme";
import fetchData from "./utils/fetchData";
import Main from './components/pages/Index';
import { createRoot } from 'react-dom/client';
import { useMediaQuery } from '@mui/material';
import { SnackbarProvider } from 'notistack';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { BrowserRouter as Router, useRoutes } from 'react-router-dom'

import _ from '../config'
import "./less/index.less";

const pages = (ctx => {
    let keys = ctx.keys();
    let values = keys.map(ctx);
    return keys.reduce((o, k, i) => { o[k] = values[i]; return o; }, {});
})(require.context('./components/pages', true, /.jsx?/));

window.SuperGlobal = window.SuperGlobal || {};// ,   ,   

$(function(){//    - $

    function App({ }){// 
        window.SuperGlobal.darkMode = useState(localStorage.getItem('theme') ?? false);// 
        
        const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');//  
        const theme = React.useMemo(() => createTheme(style(window.SuperGlobal.darkMode[1] || prefersDarkMode)), [prefersDarkMode, window.SuperGlobal.darkMode[0]]);//
        
        window.SuperGlobal.auth = useState(localStorage.getItem('movieToken') ? true : false);//
        window.SuperGlobal.syncProfile = useState({});
        
        useMemo(() => {
            if (window.SuperGlobal.auth[0]){
                window.SuperGlobal.token = localStorage.getItem('movieToken');
                window.SuperGlobal.profile = fetchData(new URL(`/api/account/profile`, _.api_server), {}, 'GET').catch(() => {
                    //enqueueSnackbar(e.message, { variant: 'error' });
                });
                
                (async () => {
                    window.SuperGlobal.syncProfile[1](await window.SuperGlobal.profile);
                })();
            }
        }, [window.SuperGlobal.auth[0]]);//
        
        let NotFound = useMemo(() => {
            return () => {
                return <>
                    <span>404 Not Found</span>
                </>;
            };
        }, []);
        
        let RouteApp = () => useRoutes([//   
            { path: '/', element: <Main /> },
            { path: '/index.html', element: <Main /> },
            ...(Object.entries(pages).map((e) => {// 
                let [key, El] = e;
                
                return {
                    path: key.match(/(\w+)\.jsx?/)[1].toLowerCase(),
                    element: <El.default />,
                };
            })),
            // { path: '/movie/:id', element: <Movie/> },//  ,  id
            // { path: '/:id', element: <Main /> },
            { path: '*', element: <NotFound /> },
        ]);
        
        console.log(theme);
        
        return (
            <ThemeProvider theme={theme}>
                <SnackbarProvider maxSnack={3}>{/*  usesnackbar*/}
                    <Router>
                        <RouteApp />
                    </Router>
                </SnackbarProvider>
            </ThemeProvider>
        );
    }

    createRoot($('root')[0]).render(<App />);// 
});